body {
    // margin: 0;
    // background: rgb(0, 0, 0);
    background-image: linear-gradient(to right, rgb(62, 43, 62), rgb(132, 52, 252) );
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 85vh;
}

#hidden-canvas {
  position: absolute;
  top: 83px;
  // z-index: 15;
}

.ink-bar {
    position: absolute;
    top: 100px;
    left: 200px;
    background-color: grey;
    border-radius: 20px;
    height: 30px;
    width: 300px;
    z-index: 1;
  }
  
  .ink-icon {
    position: absolute;
    width: 20px;
    height: 20px;
    margin-top: 5px;
    margin-left: 10px;
    z-index: 2;
  }
  
  .ink-full {
    background-image: repeating-linear-gradient(to left, #0546e0, #1172f9, #4892f9);
    box-shadow: 0 5px 5px -6px #0546e0;
    border-radius: 20px;
    color: white;
    height: 100%;
    width: 0;
    transition: 1s ease 0.3s;
    position: relative;
    z-index: 1;
  }

h1 {
    color: whitesmoke;
    font-family: "Brush Font"
}
#l {
    font-style: italic;
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

