.modal-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  background-color: rgb(82, 202, 192);
  background-image: url("../assests/ghost-right1.png");
  background-repeat: no-repeat;
  background-blend-mode:screen;
  border-radius: 15px;
  border-style: groove;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  width: 60%;
  height: 60%;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal h2 {
  margin-bottom: 20px;
  font-weight: bold;
  font-family: "Brush Font";
}

.modal button {
background-color: rgb(38, 165, 51);
color: black;
font-size: 16px;
padding: 10px 20px;
border-radius: 5px;
cursor: pointer;
}

.modal button:hover {
  background-color: #3e8e41;
  color: yellow;
}

.start-tips {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.tips-column {
  width: 50%;
}

.start-ink-icon,
.spike-icon,
.drywall-icon,
.arrows-icon {
  height: 50px;
  width: auto;
  margin-right: 10px;
}

.inkbar-icon {
  height: 25px;
  width: auto
}

.desc {
  font-size: 16px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
}

.play-column {
  width: 45%;
}

@media only screen and (max-width: 600px) {
  .start-tips {
    flex-direction: column;
  }

  .tips-column,
  .play-column {
    width: 100%;
    margin-bottom: 20px;
  }
}

